.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70vh;
}

.loader__spinner {
  border: 10px solid #ffffffdc;
  border-top: 10px solid #171717;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
